* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button {
  height: 40px;
  width: 72px;
  font-size: 25px;
  border-radius: 10px;
  border: 1px solid transparent;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
  background: #3c4044;
  color: #e8eaed;
  margin-top: 3px;
  margin-right: 3px;
  text-align: center;
  font-weight: 400;
  line-height: 34px;
  cursor: pointer;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'arial', sans-serif;
}

.button:active {
    position:relative;
    bottom: 1px;
}

#equalButton{
    /* background: #AECBFA; */
    background: #00E6F6;
    color: #202124
}

#operationButton{
    /* background: #70757A; */
    background: #1F6CAB;
}
  
#zeroButton{
    width: 147px;
    margin-right: 3px;
}

  



.basicOpContainer{
    align-items: flex-end;
    width: 25%;
    justify-content: flex-start;
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px); 
}
    
.buttonContainer{
    display: flex;
    width: 90%;
    margin-inline: auto;
    align-items: flex-end;
    height: 60%;
}

.calcContainer{
    width: 350px;
    height: 500px;
    /* background: #202124; */
    background: #082A3A;
    border: #ffffff, solid, 2px;
    border-radius: 40px;
    margin: 50px;  
}

.displayContainer{
    width: 100%;
    height: 35%;
    justify-content: center;
}

.numberContainer{
    display: flex;
    width: 75%;
    justify-content: center;
    align-items: flex-end;
    flex-wrap: wrap;
    margin: 0;
    
}

.parenthesesPrompt{
    position: relative;
    color: #e8eaed;
    width: 80%;
    padding: 3px;
    top:5%;
    margin-inline: auto;
    text-align: center;
    border-radius: 10px;
    font-weight: bold;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'arial', sans-serif;

}

#non-visible{
    visibility: hidden;
}

#numberInputs{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    flex-wrap: wrap;
}

.display{
    display: flex;
    margin-inline: auto;
    width: 90%;
    height: 100%;
    justify-content: flex-end;
    align-items: flex-end;
    background: transparent;
    border-radius: 20px;
    font-size: 30px;
    padding: 6px;
    overflow: hidden;
    color: #e8eaed;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'arial', sans-serif;
    
}

#textId{
    font-size: 30px;
}

.App{
    min-height: 100vh;
    width: 100%;
    background: blanchedalmond;
    margin: 0;
}
h1{
    margin: 0px;
    text-align: center;
    padding-top: 10px;
    
}

header{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    font-size: xx-large;
}
main{
  
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    min-height: 100vh;
}

@media only screen and (max-width: 1000px) {
    main {
        flex-direction: column;
    }
  }
h2{
    margin: 0;
    text-align: center;
}
ul{
    height: 100%;
    background: #3c4044;
    border-radius: 10px;
    padding-left: 0;
    margin: 0;
    padding-right: 10px;

}

.definition{
    position: relative;
    height: 40px;
    display: flex;
    font-size: 12px;
    color: #e8eaed;
    justify-content: flex-start;
    align-items: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'arial', sans-serif;
}

.definitionContainer{
    width: 90%;
    height: 100%;
    display: flex;
    
}

.instructionContainer{
    width: 500px;
    height: 500px;
    margin: 0;
    -webkit-transform: translateY(-28px);
            transform: translateY(-28px);
}

.instructionContext{
    display: flex;
    width: 100%;
    overflow: visible;
}

.instructionSymbols {
    height: 30px;
    width: 50px;
    font-size: 15px;
    margin-top: 3px;
    border-radius: 10px;
    border: 1px solid transparent;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
    color: #e8eaed;
    text-align: center;
    font-weight: 400;
    line-height: 30px;
    cursor: pointer;
    justify-content: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'arial', sans-serif;
}

.symbolContainer{
    margin-right: 3%;
    margin-left: 10px;
    width: 10%;
    height: 100%;
}


